import React from 'react';
import PropTypes from 'prop-types';
import { reduxForm, Field } from 'redux-form';

import { withStyles } from '@material-ui/core/styles';

import style from './styles';
import InputField from '../InputField';

const LoginForm = (props) => {
  const { 
    email, 
    password, 
    emailOnChange, 
    passwordOnChange, 
    emailValidation,
    passwordValidation,
    requiredValidation,
    onSubmit,
    classes,
    handleSubmit
  } = props;

  return (
    <div className={classes.root}>
      <form className={classes.form} onSubmit={handleSubmit(onSubmit)} >
       <Field
          component={InputField}
          name='email'
          type='email'
          label='  email'
          validate={[requiredValidation, emailValidation]}
          onChange={emailOnChange}
        /> 
        <Field
          component={InputField}
          name='password'
          type='password'
          label='  '
          validate={[requiredValidation, passwordValidation]}
          onChange={passwordOnChange}
        />
        <button className={classes.button}></button>
      </form>
      <pre>{ JSON.stringify({email, password}) }</pre>
    </div>
  )
}

LoginForm.propTypes = {
  email: PropTypes.string.isRequired,
  password: PropTypes.string.isRequired,
  onSubmit: PropTypes.func.isRequired,
  emailValidation: PropTypes.func.isRequired,
  passwordValidation: PropTypes.func.isRequired,
  requiredValidation: PropTypes.func.isRequired,
  emailOnChange: PropTypes.func.isRequired,
  passwordOnChange: PropTypes.func.isRequired,
}

const LoginFormWithStyle = withStyles(style)(LoginForm);

export default reduxForm({
  form: 'login',
})(LoginFormWithStyle);



